function faq() {
    $('.faq').each(function(){
        var $this = $(this),
            $answer = $this.find('.faq-answer'),
            shown = $this.data('shown');

        if(shown) {
            $answer.show();
        }
    });
    $(document).on('click', '.faq-question', function() {
        var $this = $(this),
            $faq = $this.closest('.faq'),
            $answer = $faq.find('.faq-answer')

        $answer.slideToggle();
    });
}